import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // ãããªã®ãã¼ã¿ãæ ¼ç´ããéå
    videos: [
      {
        index: 0,
        videoId: 'fuCzjM1gAuM'
      }
    ],
    // ç¾å¨ã®ãããªçªå·
    currentVideoIndex: 0,
    // ç¾å¨è¡¨ç¤ºããã¦ãããããªã®YouTubeã®ãããªID
    currentVideoId: 'fuCzjM1gAuM',
    // æ¬¡ã®ãããªãå­å¨ãããã©ãã
    // ã¤ã¾ãè¡¨ç¤ºããã¦ããã®ãæå¾ã®ãããªãã©ãã
    isNextVideo: true,
    // åã®ãããªãå­å¨ãããã©ãã
    // ã¤ã¾ãè¡¨ç¤ºããã¦ããã®ãæåã®ãããªãã©ãã
    isPrevVideo: false,
    // YouTube API KEY
    ytApiKey: 'AIzaSyBWLYMMmxhIcc_gIUbC7AOmojTLIuYQJIk',
    // YouTube PlayListId
    ytPlaylistId: 'PLuFk1iO9RvJVEeO6a3bOmTUwx1jaCdRlh',
    // YouTube Playlist Data
    ytPlaylistData: null,
    // APIãããã¼ã¿åå¾ããéã«çºçããã¨ã©ã¼ãªãã¸ã§ã¯ã
    isYtLoadError: null
  },
  mutations: {
    /**
     * ç¾å¨ã®ãããªãæ´æ°ãã
     */
    updateCurrentVideo(state, videoIndex: number) {
      // ãããªçªå·ã¨YouTubeã®ãããªIDãæ´æ°
      state.currentVideoIndex = videoIndex;
      state.currentVideoId = state.videos[videoIndex].videoId;
      // ãã©ã°ãã¢ãããã¼ã
      const lastIndex = state.videos.length - 1;
      state.isNextVideo = !(state.currentVideoIndex >= lastIndex);
      state.isPrevVideo = (state.currentVideoIndex > 0);
    }
  },
  actions: {
    /**
     * æ¬¡ã®ãããªã¸
     */
    updateNext({ commit, state }) {
      if (state.isNextVideo) {
        const vIndex: number = state.currentVideoIndex + 1;
        commit('updateCurrentVideo', vIndex);
        console.log('Update Next Video');
      }
    },
    
    /**
     * åã®ãããªã¸
     */
    updatePrev({ commit, state }) {
      if (state.isPrevVideo) {
        const vIndex: number = state.currentVideoIndex - 1;
        commit('updateCurrentVideo', vIndex);
        console.log('Update Previous Video');
      }
    },
    
    /**
     * YouTube APIãããã¬ã¤ãªã¹ãã®ãã¼ã¿ãã­ã¼ããã
     */
    loadPlaylistData({ commit, state }) {
      // APIãã³ã¼ã«ããã¯ã¨ãªURLãä½æ
      const queryUrl: string = 'https://www.googleapis.com/youtube/v3/playlistItems?'
        + 'part=snippet'
        + '&maxResults=20'
        + '&playlistId=' + state.ytPlaylistId
        + '&key=' + state.ytApiKey;
      
      // axiosãä½¿ã£ã¦Ajaxã§YouTubeã®ãã¬ã¤ãªã¹ããåå¾ãã
      axios.get(queryUrl)
        // æåæ
        .then((response) => {
          // åå¾ãããã¼ã¿ãä¸æä¿å­
          state.ytPlaylistData = response.data;
          // ãã¼ã¿ããå¿è¦ãªæå ±ã ããä½¿ã£ã¦å å·¥ãã
          const items: any[] = state.ytPlaylistData.items;
          const itemAmount: number = items.length;
          const videos: any[] = [];
          // ãã¹ã¦ã®ãããªããYouTubeã®ãããªIDã ããä½¿ã
          for (let i: number = 0; i < itemAmount; i++) {
            const videoData = {
              index: i,
              videoId: items[i].snippet.resourceId.videoId
            };
            videos.push(videoData);
          }
          // ã¹ãã¼ãã®ãããªãã¼ã¿ãªã¹ããæ´æ°
          state.videos = videos;
        })
        // å¤±ææ
        .catch((error) => {
          state.isYtLoadError = error;
        });
    }
  }
});
